/* Бокове меню Пк */
.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  width: var(--sidebar-width);
  height: 100vh;
  background-color: var(--sidebar-bg-color);
  border-right: 1px solid var(--sidebar-border-color);
  padding: var(--sidebar-padding);
  box-sizing: border-box;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: var(--sidebar-z-index);
}

/* Логотип у меню */
.logo-container {
  margin-bottom: var(--logo-container-margin-bottom);
  width: 100%;
  display: flex;
  justify-content: center;
}

.logo-container img {
  max-width: 100%;
  height: auto;
}

/* Список пунктів меню */
.sidebar ul {
  width: 100%;
  list-style: none;
  padding: 0;
  margin: 0;
}

.sidebar ul li {
  margin-bottom: 8px;
}

/* Кнопки меню ПК */
.sidebar ul li a.sidebar-btn {
  display: block;
  padding: var(--sidebar-btn-padding);
  color: var(--sidebar-text-color);
  text-decoration: none;
  font-size: var(--sidebar-btn-font-size);
  border-radius: var(--sidebar-btn-border-radius);
  transition: background-color 0.2s ease;
  text-align: center;
}

/* Кнопки меню ПК */
.sidebar ul li a.sidebar-btn:hover,
.sidebar ul li a.sidebar-btn:focus {
  background-color: var(--sidebar-btn-hover-bg);
  outline: none;
}

.sidebar ul li a.sidebar-btn.active {
  background-color: var(--sidebar-btn-active-bg);
  font-weight: var(--sidebar-btn-active-font-weight);
}

/* Бокове меню */
.mobile-sidebar {
  position: fixed;
  top: 0;
  left: -260px;
  width: 240px;
  height: 100vh;
  background-color: var(--sidebar-bg-color);
  border-right: 1px solid var(--sidebar-border-color);
  padding: var(--sidebar-padding);
  box-sizing: border-box;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: var(--sidebar-z-index);
  transition: left 0.3s ease;
}

.mobile-sidebar ul {
  width: 100%;
  list-style: none;
  padding: 0;
  margin: 0;
}

.mobile-sidebar ul li {
  margin-bottom: 8px;
}

.mobile-sidebar ul li a.sidebar-btn {
  display: block;
  padding: var(--sidebar-btn-padding);
  color: var(--sidebar-text-color);
  text-decoration: none;
  font-size: var(--sidebar-btn-font-size);
  border-radius: var(--sidebar-btn-border-radius);
  transition: background-color 0.2s ease;
  text-align: center;
}

.mobile-sidebar ul li a.sidebar-btn:hover,
.mobile-sidebar ul li a.sidebar-btn:focus {
  background-color: var(--sidebar-btn-hover-bg);
  outline: none;
}

.mobile-sidebar ul li a.sidebar-btn.active {
  background-color: var(--sidebar-btn-active-bg);
  font-weight: var(--sidebar-btn-active-font-weight);
}
